import { createChatBotMessage } from "react-chatbot-kit";
import Options from "./Options";

const config = {
  botName: "Asistente Legal",
  initialMessages: [
    createChatBotMessage("Bienvenido a Bufete Salas Granados. Soy su asistente virtual, ¿en qué puedo ayudarle hoy?", {
      widget: "options",
    }),
  ],
  widgets: [
    {
      widgetName: "options",
      widgetFunc: (props) => <Options {...props} />,
    },
  ],
  customStyles: {
    botMessageBox: {
      backgroundColor: "#376B7E",
    },
    chatButton: {
      backgroundColor: "#376B7E",
    },
    container: {
      width: '100%',
      maxWidth: '350px',
    },
    chatContainer: {
      display: 'flex',
      flexDirection: 'column',
    },
    inputContainer: {
      borderTop: '1px solid #e5e5e5',
      padding: '10px',
    },
  },
};

export default config;