// src/components/chatbot/ActionProvider.js
class ActionProvider {
    constructor(createChatBotMessage, setStateFunc) {
      this.createChatBotMessage = createChatBotMessage;
      this.setState = setStateFunc;
    }
  
    handleServices = () => {
      const message = this.createChatBotMessage(
        "Nuestro bufete ofrece una amplia gama de servicios legales especializados, incluyendo:\n\n" +
        "1. Derecho Corporativo\n" +
        "2. Propiedad Intelectual\n" +
        "3. Derecho Laboral\n" +
        "4. Derecho Ambiental\n" +
        "5. Litigios y Arbitraje\n\n" +
        "¿Sobre cuál área le gustaría obtener más información?"
      );
      this.updateChatbotState(message);
    };
  
    handleContact = () => {
      const message = this.createChatBotMessage(
        "Puedes contactarnos llamando al +506 6068-3184 o enviando un email a lic.rogersalasg@gmail.com. También puedes agendar una cita a través de nuestra página de contacto."
      );
      this.updateChatbotState(message);
    };
  
    handlePricing = () => {
      const message = this.createChatBotMessage(
        "Nuestras tarifas varían dependiendo del tipo y complejidad del caso. Ofrecemos una consulta inicial gratuita para evaluar tu situación y proporcionar un presupuesto personalizado."
      );
      this.updateChatbotState(message);
    };
  
    handleDefault = () => {
      const message = this.createChatBotMessage(
        "Lo siento, no entendí tu pregunta. ¿Podrías reformularla o elegir una de las opciones disponibles?"
      );
      this.updateChatbotState(message);
    };
  
    handleCorporateLaw = () => {
      const message = this.createChatBotMessage(
        "Nuestro equipo de derecho corporativo ofrece asesoramiento integral en fusiones y adquisiciones, constitución de sociedades, gobierno corporativo y cumplimiento normativo. ¿Tiene alguna consulta específica en esta área?"
      );
      this.updateChatbotState(message);
    };

    handleIntellectualProperty = () => {
      const message = this.createChatBotMessage(
        "Nuestro equipo de propiedad intelectual se especializa en proteger y gestionar los activos intangibles de nuestros clientes, incluyendo patentes, marcas registradas, derechos de autor y secretos comerciales. ¿Tiene alguna consulta específica sobre propiedad intelectual?"
      );
      this.updateChatbotState(message);
    };

    updateChatbotState(message) {
      this.setState((prevState) => ({
        ...prevState,
        messages: [...prevState.messages, message],
      }));
    }
  }
  
  export default ActionProvider;